@font-face{
    font-family: "webcafeIcon";
    font-weight: 400;
    src: url("font/webcafeIcon.eot?#iefix") format("embedded-opentype"),
    url("font/webcafeIcon.woff") format("woff"),
    url("font/webcafeIcon.ttf") format("truetype"),
    url("font/webcafeIcon.svg#webcaferegular") format("svg");
}
@font-face{
    font-family: "NotoSan";
    font-weight: 400;
    src: url("font/NotoSansKR-Medium.otf") format("truetype");
}


*{
    max-width: 1280px;
    margin: auto;
    color: rgb(39, 39, 39);
    font-family: NotoSan;
}
.hidden{
    display: none;
}
a{
    text-decoration: none;
}
ul{
    list-style: none;
}


header{
    position: relative;
}
/*로고*/
.logo{
    width: 1280px;
    text-align: center;
    height: 200px;
}
.logo img {
    width: 300px;
    position: relative;
    top: 68.765px;
}

/*인포링크*/
.infoLink{
    position: absolute;
    top: 20px;
    right: 20px;
}
.infoLink li {
    display: inline-block;
    padding: 5px 10px 0 0;
    font-size: 0.8em;
}
.infoLink a::before {
    content: "·";
    position: relative;
    padding-right: 10px;
    font-weight: bold;
    bottom: 1px;
}
.infoLink li:first-child a::before {
    content: "";
}

/*메뉴*/
nav{
    height: 50px;
    width: 1280px;
    margin: 0;
    padding: 0;
    margin-bottom: 20px;
}
.Menu{
    text-align: center;
    margin: 0 auto;
}
.Menu >li{
    float: left;
    line-height: 50px;
    width: 200px;
    font-size: 1.2em;
}
.Menu ul {
    position: absolute;
    background-color: #fff;
    width: 200px;
    padding: 0;
    height: 230px;
    display: none;
}
.menu ul:first-child{
    padding-left: 50px;
}
.Menu:hover ul {
    display: block;
}
.Menu li > a:hover, .Menu li>a:focus, .Menu > li:hover > a{
    color: rgb(0,137,57);
}
.Menu li >a:hover, .Menu li>a:focus{
    text-decoration: underline;
}
.Menu > li:hover > a {
    font-weight: bold;
    text-decoration: none;
}
.Menu ul > li {
    font-size: 0.7em;
    height: 35px;
}

/*banner*/
.banner{
    width: 1280px;
    background-color: rgb(0,137,57);
    height: 250px;
    text-align: center;
    margin: auto;
    border-radius: 0 20px;
}
.banner > h3{
    font-weight: bold;
    line-height: 250px;
    font-size: 4em;
    color: rgb(243, 243, 243);
    letter-spacing: 40px;
    padding-left: 40px;
}

/*서브메뉴*/
.subMenu {
    float: left;
    height: 800px;
    width: 210px;
    padding: 0;
    margin: 0;
    padding-top: 100px;
}
.subMenu ul li {
    height: 70px;
    line-height: 70px;
}
.subMenu p{
    display: inline-block;
    position: relative;
    bottom: 15px;
    padding-left: 20px;
}
.subMenu li:hover, .subMenu li:focus {
    border: 6px solid rgb(159, 199, 176);
    border-right: none;
    border-radius: 50px 0 0 50px;
}
.menuClick{
    border: 6px solid rgb(0,137,57);
    border-right: none;
    border-radius: 50px 0 0 50px;
}

.other{
    background-color: rgb(241, 241, 241);
    border-radius: 50px 0 0 50px;
}
.other p{
    color: rgb(114, 114, 114);
}
.subMenu .navlink {
    display: inline-block;
    width: 100%;
    font-size: 1.5em;
    position: relative;
}
.subMenu .check{
    font-family: 'webcafeIcon';
    color: #ccc;
    display: inline-block;
    width: 40px;
    height: 100%;
    font-size: 1.8em;
    font-weight: bold;
    position: relative;
    left: 10px;
}
