    color: #ccc;
    position: absolute;
    left: -35px;
    font-size: 1.8em;
    font-weight: bold;
}*/


/*메인페이지*/
.MP{
    width: 900px;
    float: left;
    padding-top: 70px;
    margin-bottom: 200px;
    padding-left: 100px;
}
.MP ul{
    text-align: center;
    float: left;
}
.MP li{
    float: left;
    position: relative;
    margin-bottom: 50px;
    cursor: pointer;
    margin-right: 10px;
}
.MP img{
    width: 270px;
}
.MP .check2{
    position: absolute;
    top: 0;
    right: 0;
}
.MP li p {
    text-align: center;
}
.check2{
    font-family: 'webcafeIcon';
    color: #ccc;
    display: inline-block;
    width: 50px;
    height: 50px;
    font-size: 3em;
    font-weight: bold;
}
/*.check2::before {
    font-family: 'webcafeIcon';
    content: 'c';
    color: #ccc;
    position: absolute;
    left: -50px;
    font-size: 3em;
    font-weight: bold;
}*/
.all:hover >.check2{
    color:red;
}



/*배달*/
.final{
    width: 900px;
    padding-top: 100px;
    margin-bottom: 200px;
    margin-left: 300px;
    position: relative;
}
.final div{
    border-top: 5px solid rgb(242, 183, 1);
    padding-top: 30px;
    margin-bottom: 100px;
}
.final div:first-child{
    border: none;
}
.final h4{
    font-size: 2.5em;
    font-weight: bold;
    color: rgb(0,137,57);
    background-color: #fff;
    width: max-content;
    margin: 0;
    padding-left: 30px;
    margin-bottom: 50px;
}
.myInfo p{
    padding-left: 100px;
    font-size: 1.2em;
    margin-top: 50px;
}
.myInfo p span{
    display: inline-block;
    width: 100px;
    font-weight: bold;
    color: rgb(94, 94, 94);
    margin-right: 50px;
}
.myInfo p input{
    outline: none;
    border: none;
    border-bottom: 3px solid rgb(163, 163, 163);
    width: 300px;
    padding: 10px;
    font-size: 1.2em;
}
.myInfo p:last-child input{
    width: 500px;
}
.please textarea{
    margin-left: 100px;
    font-size: 1.6em;
    width: 650px;
    height: 150px;
    outline: none;
}
.orderInfo{
    padding-bottom: 50px;
}
.cost p{
    margin-left: 100px;
    font-size: 1.2em;
}
.cost p{
    margin-bottom: 40px;
}
.cost .title{
    font-weight: bold;
    width: 200px;
    display: inline-block;
    color: rgb(94, 94, 94);
}
.btnF{
    text-align: center;
}
.btnF button{
    width: 600px;
    height: 150px;
    font-size: 2em;
    font-weight: bold;
    color: rgb(48, 48, 48);
    background-color: rgb(0,137,57);
    border: 5px solid rgb(242, 183, 1);
    border-radius: 10px;
    margin-top: 50px;
    cursor: pointer;
}
.btnF .cost{
    color: rgb(241, 241, 241);
    margin-right: 20px;
}